/* Sidebar Styles */
.sidebar {
    width: 240px;
    background-color: #4CAF50; /* Green background */
    box-shadow: 0 0 2px gray;
    padding: 20px;
    height: 100vh; /* Full viewport height */
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start; /* Align items to the top */
    position:fixed; /* Sidebar is fixed to the left */
    top: 0;
    left: 0;
    margin: 5px ;
    z-index: 1000; /* Ensures it is on top of other elements */
}

.logo h5 {
    color: white;
    font-size: 24px;
    text-align: center;
    margin-bottom: 30px;
}

.menu {
    display: flex;
    flex-direction: column;
}

.menu-item {
    display: flex;
    align-items: center;
    padding: 10px;
    margin-bottom: 10px;
    color: white;
    text-decoration: none;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s ease;
}

.menu-item.active,
.menu-item:hover {
    background-color:#388E3C; /* Darker green for active and hover state */
}

.menu-item i {
    margin-right: 10px;
    font-size: 20px;
}

.menu-item span {
    font-size: 18px;
    font-weight: 500;
}

.menu-item a {
    color: white;
    text-decoration: none;
}

.menu-item a:hover {
    text-decoration: none;
}
/* Layout Styles */
.main {
    padding: 10px;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.layout {
    display: flex;
   margin-top: 2px;
   margin-left: -10px;
}

.header {
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px gray;
    background-color: white;
    position: fixed; /* Fixed header */
    top: 0;
    left: 250px; /* Align with the content area */
    right: 0;
    z-index: 999; /* Ensure it stays above content */
}

.body {
    margin-top: 10vh; /* Push content down below the header */
    padding: 20px;
    height: calc(100vh - 10vh); /* Adjust body height to fit under header */
    margin-left: 250px; /* Align with sidebar */
    box-shadow: 0 0 2px gray;
    background-color: white;
}

.header-content {
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0 15px;
}

.header-content i {
    margin-right: 20px;
    font-size: 1.2rem;
}

.header-content a {
    text-decoration: none;
    font-size: 1.2rem;
    color: blue;
    margin: 0 10px;
    text-transform: uppercase;
}

.active {
    background-color: white;
    padding: 10px;
}

.active a,
.active i {
    color:white /* Color change for active state */
}

.content {
    width: 100%;
    height: 100%;
}
